import { Component, Input, OnInit } from '@angular/core';

const PURCHASE_NOTE = {
    '01': { ordinance: '', limit: '23:00' },
    '02': { ordinance: '', limit: '23:00' },
    '06': { ordinance: '', limit: '23:00' },
    '07': { ordinance: '', limit: '23:00' },
    '08': { ordinance: '', limit: '23:00' },
    '09': { ordinance: '', limit: '23:00' },
    '10': { ordinance: '', limit: '23:00' },
    '12': { ordinance: '', limit: '23:00' },
    '13': { ordinance: '', limit: '23:00' },
    '14': { ordinance: '', limit: '23:00' },
    '15': { ordinance: '', limit: '23:00' },
    '16': { ordinance: '', limit: '23:00' },
    '17': { ordinance: '', limit: '23:00' },
    '18': { ordinance: '', limit: '23:00' },
    '19': { ordinance: '', limit: '23:00' },
    '20': { ordinance: '', limit: '23:00' },
    '21': { ordinance: '', limit: '23:00' }
};

@Component({
    selector: 'app-purchase-note',
    templateUrl: './purchase-note.component.html',
    styleUrls: ['./purchase-note.component.scss']
})
export class PurchaseNoteComponent implements OnInit {

    @Input() public theaterCode: string;
    public ordinance: string;
    public limit: string;
    constructor() { }

    public ngOnInit() {
        this.ordinance = '';
        this.limit = '';
        if ((<any>PURCHASE_NOTE)[this.theaterCode] !== undefined) {
            this.ordinance = (<any>PURCHASE_NOTE)[this.theaterCode].ordinance;
            this.limit = (<any>PURCHASE_NOTE)[this.theaterCode].limit;
        }
    }

}
